/* This is the main style sheet */
h1, p, div {
    font-family: Roboto;
    font-size: 16px;
    color: #4f4f4f;
}

header {
    grid-area: header;
    background-color: #f8f8f8;
    padding: 2% 1% 0 1%;
  }

h1 {
    font-size: 48px;
    line-height: 1.04;
    color: #000000;
    margin-left: 16%;
}

#description {
    line-height: 1.25;
    width: 50%;
    margin-right: 54%;
    margin-left: 16%;
  }

/* slider format */

#time-slider-container {
    padding: 0 2%;
}

#time-slider {
    width: 100%
}

#time-slider-label {
    padding-top: 1%;
    padding-left: 1%;
    display: grid;
    grid-template-columns: repeat(36, 1fr);
}

#time-slider-label > div {
    text-align: center;
    vertical-align: middle;
}

#month-year {
    margin-top: 1%;
    text-align: center;
}

/* css grid settings */

body, .grid-container {
    display: grid;
    grid-template-areas: "header    header  header"
                         "sidebar   content content"
                         "sidebar   content content"
                         "sidebar   content content";
    height: 600px;
}
.grid-element.content {
    grid-area: content;
}

.grid-element.sidebar {
    grid-area: sidebar;
}

